/* ========================
   전역 폰트 설정
======================== */
* {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif !important;
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;
  text-rendering: optimizeLegibility !important;
}

/* ========================
   메인 페이지 기본
======================== */
.main-page {
  background: var(--bg);
  min-height: 100vh;
  color: var(--text);
  position: relative;
  overflow-x: hidden;
}

/* ========================
   로딩 및 에러 상태
======================== */
.loading-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  font-size: 18px;
  color: var(--text);
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: var(--radius-md);
  margin: 20px 0;
}

.no-data {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  font-size: 18px;
  color: var(--text);
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: var(--radius-md);
  margin: 20px 0;
}

.error-message {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 200px;
  background: var(--surface);
  border: 1px solid var(--danger);
  border-radius: var(--radius-md);
  margin: 20px 0;
  padding: 20px;
  text-align: center;
}

.error-message p {
  margin: 5px 0;
  color: var(--danger);
  font-size: 16px;
}

.error-message p:first-child {
  font-weight: bold;
  font-size: 18px;
}

.error-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 60vh;
  text-align: center;
  padding: 40px;
}

.error-container h2 {
  color: var(--danger);
  margin-bottom: 20px;
  font-size: 24px;
}

.error-container p {
  color: var(--text);
  margin-bottom: 30px;
  font-size: 16px;
  max-width: 500px;
}

.error-container button {
  background: var(--danger);
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 25px;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.error-container button:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(255, 107, 107, 0.4);
}

/* 폴백 데이터 알림 */
.fallback-notice {
  background: linear-gradient(135deg, rgba(255, 193, 7, 0.1), rgba(255, 152, 0, 0.1));
  border: 1px solid rgba(255, 193, 7, 0.3);
  border-radius: 10px;
  padding: 15px 20px;
  margin: 20px 0;
  text-align: center;
  color: var(--text);
}

.fallback-notice p {
  margin: 5px 0;
  font-size: 14px;
  color: #ffc107;
}

.fallback-notice p:first-child {
  font-weight: bold;
  font-size: 16px;
}

/* ========================
   커튼 배경 요소들
======================== */
/* 좌측 커튼 */
.main-page::before {
  content: '';
  position: fixed;
  top: 0;
  left: -50px;
  width: 200px;
  height: 100vh;
  background: linear-gradient(90deg, 
    rgba(139, 0, 0, 0.8) 0%,
    rgba(139, 0, 0, 0.6) 30%,
    rgba(139, 0, 0, 0.3) 70%,
    transparent 100%);
  transform: skewX(-15deg);
  z-index: 1;
  animation: curtainLeft 8s ease-in-out infinite;
}

/* 우측 커튼 */
.main-page::after {
  content: '';
  position: fixed;
  top: 0;
  right: -50px;
  width: 200px;
  height: 100vh;
  background: linear-gradient(-90deg, 
    rgba(139, 0, 0, 0.8) 0%,
    rgba(139, 0, 0, 0.6) 30%,
    rgba(139, 0, 0, 0.3) 70%,
    transparent 100%);
  transform: skewX(15deg);
  z-index: 1;
  animation: curtainRight 8s ease-in-out infinite;
}

/* 상단 커튼 */
.main-page .top-curtain {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 120px;
  background: linear-gradient(180deg, 
    rgba(139, 0, 0, 0.9) 0%,
    rgba(139, 0, 0, 0.7) 50%,
    rgba(139, 0, 0, 0.3) 80%,
    transparent 100%);
  z-index: 1;
  animation: topCurtain 10s ease-in-out infinite;
}

/* 커튼 애니메이션 */
@keyframes curtainLeft {
  0%, 100% { transform: skewX(-15deg) translateX(0); }
  50% { transform: skewX(-15deg) translateX(-20px); }
}

@keyframes curtainRight {
  0%, 100% { transform: skewX(15deg) translateX(0); }
  50% { transform: skewX(15deg) translateX(20px); }
}

@keyframes topCurtain {
  0%, 100% { opacity: 0.9; }
  50% { opacity: 0.7; }
}

/* 배경 커튼 장식 */
.main-page .curtain-decoration {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 100vh;
  pointer-events: none;
  z-index: 1;
}

.main-page .curtain-decoration::before {
  content: '';
  position: absolute;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
  width: 300px;
  height: 4px;
  background: linear-gradient(90deg, 
    transparent 0%,
    rgba(212, 175, 55, 0.8) 20%,
    rgba(212, 175, 55, 1) 50%,
    rgba(212, 175, 55, 0.8) 80%,
    transparent 100%);
  animation: curtainLine 6s ease-in-out infinite;
}

.main-page .curtain-decoration::after {
  content: '';
  position: absolute;
  top: 100px;
  left: 50%;
  transform: translateX(-50%);
  width: 200px;
  height: 2px;
  background: linear-gradient(90deg, 
    transparent 0%,
    rgba(139, 0, 0, 0.6) 30%,
    rgba(139, 0, 0, 0.8) 50%,
    rgba(139, 0, 0, 0.6) 70%,
    transparent 100%);
  animation: curtainLine 6s ease-in-out infinite 1s;
}

@keyframes curtainLine {
  0%, 100% { opacity: 0.6; transform: translateX(-50%) scaleX(1); }
  50% { opacity: 1; transform: translateX(-50%) scaleX(1.1); }
}

.main-container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 20px;
  position: relative;
  z-index: 2;
}

.spacer {
  height: 60px;
}

/* ========================
   Hero 섹션
======================== */
.hero {
  text-align: center;
  margin-bottom: 30px;
  padding: 20px 0;
  position: relative;
  z-index: 2;
}

/* Hero 배경 커튼 효과 */
.hero::before {
  content: '';
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
  width: 80%;
  height: 100%;
  background: radial-gradient(ellipse at center, 
    rgba(139, 0, 0, 0.1) 0%,
    rgba(139, 0, 0, 0.05) 50%,
    transparent 100%);
  z-index: -1;
  border-radius: 50%;
}

.hero h1 {
  font-size: 2.5rem;
  margin-top: 15px;
  margin-bottom: 12px;
  color: var(--gold); /* 골드 */
  font-weight: 700;
  text-shadow: 0 2px 4px rgba(0,0,0,0.3);
  position: relative;
}

.main-event-link {
  color: inherit;
  text-decoration: none;
  transition: all 0.3s ease;
  cursor: pointer;
  position: relative;
}

.main-event-link:hover {
  color: var(--gold-strong);
  transform: scale(1.05);
  text-shadow: 0 4px 12px rgba(212, 175, 55, 0.8);
}

.main-event-link::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, var(--gold), var(--gold-strong));
  transition: width 0.3s ease;
}

.main-event-link:hover::after {
  width: 100%;
}

