/* 새 위 작대기 (h1 위, 게스트 칩보다 위) */
.hero-line.top {
  width: 60px;
  height: 3px;
  margin: 6px auto 10px auto; /* 위-오-아-왼: h1과의 간격은 여기로 조절 */
  background: linear-gradient(90deg, var(--gold), var(--gold-strong));
  border-radius: 2px;
}

.hero p {
  color: #c5a572; /* 연한 골드 */
  margin-bottom: 20px;
  font-size: 1.2rem;
  font-weight: 500;
}

/* 히어로 보조 영역 */
.hero .hero-sub {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;
  margin: 0;
  flex-wrap: wrap;
}

/* 게스트 상태 칩 */
.status-chip {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 6px 10px;
  border-radius: 999px;
  font-size: 0.78rem;
  font-weight: 600;
  letter-spacing: .2px;
  background: rgba(139, 0, 0, 0.18);
  border: 1px solid rgba(212, 175, 55, 0.35);
  color: #d4c48a;
  box-shadow: 0 2px 6px rgba(0,0,0,.25) inset;
}

/* 보조 카피 */
.subcopy {
  font-size: 0.86rem;
  color: #9ca3af;
  opacity: .9;
}

/* 인라인 CTA */
.inline-cta {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  padding: 8px 16px;
  border-radius: 999px;
  font-size: 0.85rem;
  font-weight: 600;
  border: 1px solid var(--gold);
  background: linear-gradient(145deg, #302010, #4a3520);
  color: var(--text);
  text-decoration: none;
  transition: transform .15s ease, box-shadow .2s ease, background .2s ease;
  line-height: 1.2;
}

.inline-cta:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 14px rgba(212, 175, 55, .25);
  background: linear-gradient(145deg, #4a3520, #604528);
}

.cta-subtext {
  font-size: 0.7rem;
  font-weight: 400;
  color: #d4c48a;
  margin-top: 2px;
  opacity: 0.9;
}

/* 기존 hero 문구 정리 */
.hero .tagline { 
  color: #c5a572; 
  margin: 6px 0 18px 0; 
  font-size: 1.1rem; 
  font-weight: 500; 
}

/* 모바일에서 줄바꿈/간격 안정화 */
@media (max-width: 480px) {
  .hero .hero-sub { 
    gap: 12px;
  }
  .status-chip { font-size: 0.76rem; padding: 5px 9px; }
  .inline-cta { 
    font-size: 0.82rem; 
    padding: 6px 12px; 
  }
  .cta-subtext {
    font-size: 0.65rem;
  }
}

/* 포스터 */
.poster-carousel {
  display: flex;
  gap: 20px;
  justify-content: center;
  margin-bottom: 15px;
}

.poster-card {
  flex: 0 0 90%; /* 95%에서 90%로 줄여서 적절한 여백 확보 */
  max-width: 1000px; /* 1200px에서 1000px로 줄여서 적절한 크기 */
  background: linear-gradient(145deg, var(--surface), #2a2a2a); /* 어두운 그라데이션 */
  border-radius: 15px;
  box-shadow: 0 8px 25px rgba(0,0,0,0.3), 0 4px 10px rgba(0,0,0,0.2);
  padding: 20px 30px 25px 30px; /* 적절한 여백으로 조정 */
  transition: all 0.3s ease;
  border: 2px solid var(--surface-3); /* 버건디 테두리 */
  position: relative;
  overflow: hidden;
}

.poster-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, var(--gold), var(--gold-strong));
}

.poster-card:hover { 
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 15px 35px rgba(139, 0, 0, 0.3), 0 8px 15px rgba(0,0,0,0.4);
  border-color: var(--gold);
}

.poster-img {
  width: 100%; /* 700px 고정에서 100%로 변경하여 카드에 맞춤 */
  height: 450px; /* 500px에서 450px로 줄여서 적절한 비율 */
  object-fit: cover;
  border-radius: 10px;
  border: 1px solid #333;
}

.poster-link {
  display: block;
  text-decoration: none;
  transition: all 0.3s ease;
  cursor: pointer;
}

.poster-link:hover .poster-img {
  transform: scale(1.05);
  border-color: var(--gold);
  box-shadow: 0 8px 24px rgba(212, 175, 55, 0.3);
}

.poster-title {
  font-weight: bold;
  margin-top: 12px; /* 8px에서 12px로 늘려서 적절한 간격 */
  color: var(--text); /* 밝은 회색 */
  font-size: 1.3rem; /* 1.2rem에서 1.3rem으로 증가 */
}

.poster-info {
  font-size: 1.1rem; /* 1rem에서 1.1rem으로 증가 */
  color: var(--gold); /* 골드 */
  margin-top: 6px; /* 3px에서 6px로 늘려서 적절한 간격 */
  margin-bottom: 8px; /* 5px에서 8px로 늘려서 적절한 간격 */
}

.poster-sale {
  font-size: 0.9rem;
  color: #ff6b6b;
  margin-top: 4px;
  text-align: center;
  font-weight: bold;
  background: rgba(255, 107, 107, 0.1);
  padding: 2px 8px;
  border-radius: 12px;
  display: inline-block;
  border: 1px solid rgba(255, 107, 107, 0.3);
}

.poster-location {
  font-size: 0.9rem;
  color: #c5a572;
  margin-top: 4px;
  text-align: center;
  font-weight: 500;
  background: rgba(197, 165, 114, 0.1);
  padding: 4px 12px;
  border-radius: 15px;
  display: inline-block;
  border: 1px solid rgba(197, 165, 114, 0.3);
  max-width: 100%;
  word-break: break-word;
}

.slide-indicator {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-top: 15px; /* 12px에서 15px로 늘려서 적절한 간격 */
}

.slide-indicator button {
  background: linear-gradient(145deg, var(--surface), var(--surface-2));
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: var(--text);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  transition: all 0.3s ease;
  box-shadow: 0 4px 8px rgba(0,0,0,0.3);
}

.slide-indicator button:hover {
  background: linear-gradient(145deg, var(--surface), var(--surface-2));
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(139, 0, 0, 0.4);
}

.slide-indicator span {
  font-size: 1rem;
  color: var(--gold);
  font-weight: 600;
  background: rgba(139, 0, 0, 0.2);
  padding: 8px 16px;
  border-radius: 20px;
  border: 1px solid var(--surface-3);
}

/* ========================
   카테고리 그리드 (3x2)
======================== */
.section { margin-bottom: 40px; }

.cat-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  justify-items: center;
}

.cat {
  background: none;
  border: none;
  cursor: pointer;
  text-align: center;
  transition: all 0.3s ease;
}

.cat:hover {
  transform: translateY(-5px);
}

.cat-box {
  background: linear-gradient(145deg, var(--surface), var(--surface-2));
  width: 180px;
  height: 100px;
  border-radius: 15px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 6px 15px rgba(139, 0, 0, 0.3);
  border: 2px solid var(--gold);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.cat-icon {
  width: 3rem;
  height: 3rem;
  position: relative;
  z-index: 2;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.5rem;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.5));
  transition: all 0.3s ease;
}



.cat:hover .cat-icon {
  transform: scale(1.1);
}

.cat:hover .cat-icon svg {
  color: var(--gold);
  filter: drop-shadow(0 4px 8px rgba(212, 175, 55, 0.4));
}

.cat-box::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(212, 175, 55, 0.3), transparent);
  transition: left 0.5s ease;
}

.cat:hover .cat-box::before {
  left: 100%;
}

.cat:hover .cat-box {
  background: linear-gradient(145deg, var(--surface), var(--surface-2));
  box-shadow: 0 8px 20px rgba(139, 0, 0, 0.4);
  border-color: var(--text);
}

.cat-label {
  font-size: 0.95rem;
  color: var(--text);
  text-align: center;
  font-weight: 500;
  transition: color 0.3s ease;
}

.cat:hover .cat-label {
  color: var(--gold);
}

.cat-description {
  font-size: 0.8rem;
  color: #b0b0b0;
  text-align: center;
  font-weight: 400;
  margin-top: 4px;
  transition: color 0.3s ease;
  opacity: 0.8;
}

.cat:hover .cat-description {
  color: var(--gold);
  opacity: 1;
}

/* ========================
   달력 (기본 스타일)
======================== */
.cal {
  background: linear-gradient(145deg, var(--surface), #2a2a2a);
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 8px 25px rgba(0,0,0,0.3);
  border: 2px solid var(--surface-3);
  position: relative;
  overflow: hidden;
}

.cal::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, var(--gold), var(--gold-strong));
}

.cal-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  color: var(--text);
}

.cal-head h3 {
  color: var(--gold);
  font-weight: 700;
  font-size: 1.3rem;
  line-height: 1.2;
  letter-spacing: 0.5px;
}

.cal-head button {
  background: linear-gradient(145deg, var(--surface), var(--surface-2));
  border: 2px solid var(--gold);
  font-size: 1.4rem;
  cursor: pointer;
  color: var(--text);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(139, 0, 0, 0.4);
  font-weight: 600;
  line-height: 1;
  letter-spacing: 0.5px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.cal-head button:hover {
  background: linear-gradient(145deg, var(--surface), var(--surface-2));
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 8px 20px rgba(139, 0, 0, 0.5), 0 0 15px rgba(212, 175, 55, 0.3);
  border-color: var(--gold-strong);
}

.cal-head button:active {
  transform: translateY(-1px) scale(0.98);
  transition: all 0.1s ease;
}

.cal-head button svg {
  transition: all 0.3s ease;
  filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.3));
}

.cal-head button:hover svg {
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.5));
  transform: scale(1.1);
}

.cal-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 8px;
}

.cal-cell {
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.cal-dow {
  font-weight: 700;
  color: var(--gold);
  font-size: 12px;
  line-height: 1;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

/* 날짜 기본 스타일 (비활성) */
.cal-day {
  background: linear-gradient(145deg, #333, #444);
  color: #888;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid transparent;
  transition: all 0.3s ease;
  cursor: pointer;
  font-weight: 600;
  font-size: 14px;
  line-height: 1;
  letter-spacing: 0.5px;
}

.cal-day:hover {
  background: linear-gradient(145deg, #444, #555);
  color: var(--gold);
  border-color: var(--gold);
  transform: scale(1.1);
}

/* ========================
   2단 레이아웃 구성
   (좌: 달력 컴팩트 / 우: 이벤트 패널)
======================== */
.schedule {
  display: grid;
  grid-template-columns: 340px 1fr;
  gap: 30px;
  align-items: start;
  margin-top: 20px;
}

/* 달력 컴팩트 스타일 + 이벤트/마커 */
.cal.cal-compact { 
  padding: 20px; 
  background: linear-gradient(145deg, var(--surface), #2a2a2a);
}

.cal.cal-compact .cal-grid { 
  row-gap: 12px; 
}

.cal.cal-compact .cal-day {
  width: 36px; 
  height: 36px;
  background: linear-gradient(145deg, var(--surface), var(--surface-2));
  color: var(--text);
  border: 2px solid var(--gold);
  border-radius: 50%;
  display: flex; 
  align-items: center; 
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
}

.cal.cal-compact .cal-day:hover {
  background: linear-gradient(145deg, var(--surface), var(--surface-2));
  transform: scale(1.1);
  box-shadow: 0 4px 12px rgba(139, 0, 0, 0.4);
}

.cal.cal-compact .cal-day.is-selected {
  outline: 3px solid rgba(212, 175, 55, 0.6);
  box-shadow: 0 0 0 3px var(--surface) inset, 0 4px 12px rgba(139, 0, 0, 0.4);
  background: linear-gradient(145deg, var(--gold), #b8860b);
  color: var(--surface);
  border-color: var(--text);
}

.cal-dot {
  position: absolute;
  bottom: -8px;
  font-size: 12px;
  color: var(--gold);
  text-shadow: 0 1px 2px rgba(0,0,0,0.8);
}

/* 우측 이벤트 패널 */
.events-panel {
  background: linear-gradient(145deg, var(--surface), #2a2a2a);
  border: 2px solid var(--surface-3);
  border-radius: 15px;
  padding: 20px;
  box-shadow: 0 8px 25px rgba(0,0,0,0.3);
  max-height: 520px;
  overflow: auto;
  position: relative;
}

.events-panel::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, var(--gold), var(--gold-strong));
}

.events-head h3 {
  margin: 0 0 10px;
  color: var(--text);
  font-size: 1.3rem;
  font-weight: 700;
}

.events-head .weekday {
  margin-left: 8px;
  color: var(--gold);
  font-size: 1rem;
  font-weight: 500;
}

.event-list { 
  list-style: none; 
  margin: 0; 
  padding: 0; 
}

.event-item {
  display: grid;
  grid-template-columns: 1fr auto;
  align-items: center;
  gap: 15px;
  padding: 15px 8px;
  border-top: 1px solid rgba(139, 0, 0, 0.3);
  transition: all 0.3s ease;
}

.event-item:hover {
  background: rgba(139, 0, 0, 0.1);
  border-radius: 8px;
  padding-left: 12px;
}

.event-item.clickable {
  cursor: pointer;
  transition: all 0.3s ease;
}

.event-item.clickable:hover {
  background: rgba(212, 175, 55, 0.1);
  border-radius: 8px;
  padding-left: 12px;
  transform: translateX(5px);
  box-shadow: 0 4px 15px rgba(212, 175, 55, 0.2);
}

.event-item:first-child { 
  border-top: none; 
}

.event-left { 
  display: inline-flex; 
  align-items: center; 
  gap: 12px; 
}

.event-bullet { 
  color: var(--gold); 
  font-size: 14px; 
  font-weight: bold;
}

.event-title { 
  font-weight: 600; 
  color: var(--text); 
  font-size: 0.8rem;
}

.event-range { 
  color: #c5a572; 
  font-size: 0.75rem; 
  font-weight: 500;
}

.event-empty { 
  padding: 20px 8px; 
  color: #888; 
  text-align: center;
  font-style: italic;
}

/* 반응형: 작은 화면에서 1열로 */
@media (max-width: 960px) {
  .schedule { 
    grid-template-columns: 1fr; 
    gap: 20px;
  }
  
  .cat-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
  }
  
  .cat-box {
    width: 140px;
    height: 70px;
  }
  
  .cat-icon {
    width: 2rem;
    height: 2rem;
  }
  
  /* Hero 섹션 반응형 */
  .hero h1 {
    font-size: 2rem;
  }
  
  .hero p {
    font-size: 1.1rem;
  }
  
  .poster-card {
    flex: 0 0 95%;
    max-width: 95vw;
    padding: 15px 20px 20px 20px;
  }
  
  .poster-img {
    height: 350px;
  }
}

@media (max-width: 480px) {
  .cat-grid {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  
  .cat-box {
    width: 120px;
    height: 60px;
  }
  
  .cat-icon {
    width: 1.8rem;
    height: 1.8rem;
  }
  
  /* Hero 섹션 모바일 반응형 */
  .hero h1 {
    font-size: 1.8rem;
  }
  
  .hero p {
    font-size: 1rem;
  }
  
  .poster-card {
    flex: 0 0 98%;
    max-width: 98vw;
    padding: 12px 15px 18px 15px;
  }
  
  .poster-img {
    height: 280px;
  }
  
  .poster-title {
    font-size: 1.1rem;
  }
  
  .poster-info {
    font-size: 1rem;
  }
}
