/* === Global Color System - Navy × Gold Theme === */
:root {
  /* Core (Navy) */
  --bg:            #0B1220;  /* 최상위 배경(거의 검에 가까운 네이비) */
  --surface:       #0F172A;  /* 카드/헤더 기본 배경 */
  --surface-2:     #111827;  /* 한 단계 위 표면 */
  --surface-3:     #1F2937;  /* 구분/호버 표면 */

  /* Text */
  --text:          #F3F4F6;  /* 본문 기본 텍스트(밝은 회백) */
  --text-sub:      #C7CDD8;  /* 보조 텍스트 */
  --muted:         #9AA3B2;  /* 비활성/힌트 */

  /* Brand (Gold accent) — 과하지 않게 */
  --gold:          #C8A74A;  /* 메인 골드 (기존 #d4af37 톤 다운) */
  --gold-strong:   #D4B34F;  /* 강조용 골드 */
  --gold-20:       rgba(200,167,74,0.20);
  --gold-35:       rgba(200,167,74,0.35);

  /* States */
  --success:       #22C55E;
  --warning:       #EAB308;
  --danger:        #EF4444;

  /* Borders / Shadows / Rings */
  --border:        rgba(255,255,255,0.08);
  --ring:          rgba(200,167,74,0.35);
  --shadow-soft:   0 8px 24px rgba(0,0,0,0.35);

  /* Radii (통일) */
  --radius-sm:     8px;
  --radius-md:     12px;
  --radius-lg:     16px;
}

@media (prefers-color-scheme: light) {
  /* 라이트 테마가 필요하면 여기서 대비 보정 */
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}
